.aya-gallery-content {
  margin: 0 64px;
}

.aya-gallery-padding {
  display: flex;
  flex: 0 1;
  overflow: auto;
  min-height: 480px;
  width: 100%;
  align-items: center;
}

.aya-gallery-centered {
  min-height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 0.2s opacity;
  opacity: 1;
}

  .aya-gallery-centered--loading {
    opacity: 0.2;
  }

.aya-gallery-items {
  display: flex;
  min-height: min-content;
  gap: 24px;
}

.aya-gallery-item {
  width: 120px;
  height: 440px;
  position: relative;
}


.aya-gallery-item-picture-container {
  width: 120px;
  height: 400px;
  border: 2px solid white;
  margin-bottom: 16px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
}

.aya-gallery-item-picture-container-copy {
  width: 544px;
  height: 544px;
  border: 2px solid white;
  overflow: hidden;
}

.aya-gallery-item-picture-container-copy .aya-gallery-item-picture {
  width: 544px !important;
  height: 544px !important;
  transform: none;
  filter: none !important;
  transform: none !important;
}

.aya-gallery-item-picture {
  /*filter: grayscale(100%);*/
  transition: 0.3s all;
  object-fit: cover;
  object-position: center;
  transform: scale(1.2);
}

.aya-gallery-item-picture:hover {
  /*filter: grayscale(0);*/
  transform: scale(1.3);
}

.aya-gallery-item-picture-container:hover .aya-gallery-item-number {
  opacity: 1;
  transition: 0.3s all;
}

.aya-gallery-item-picture-normal {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 120px;
  height: 400px;
  transform: scale(1.4);
  border: 2px solid white;
}

.aya-gallery-item-number {
  text-align: center;
  font-size: 16px;
  font-family: Teko;
  bottom: 8px;
  right: 0;
  left: 0;
  position: absolute;

  font-family: Playfair Display;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 64px;

  color: #FFFFFF;
  opacity: 0.1;
  pointer-events: none;
}

.aya-gallery-padding::-webkit-scrollbar {
    background: rgba(196,196,196, 0.2);
    height: 4px;
}

.aya-gallery-padding::-webkit-scrollbar-thumb {
  background: #C4C4C4;
  border-radius: 2px;
}

.indiana-scroll-container--dragging {
  cursor: grabbing;
}

.aya-gallery-title {
  font-family: Teko;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 34px;
  text-align: center;

  color: #D3D3D3;
}

.aya-gallery-header {
  font-family: 'Playfair Display';
  font-style: normal;
  font-weight: bold;
  font-size: 64px;
  line-height: 85px;
  text-align: center;

  color: #D3D3D3;
}

.aya-gallery-info {
  text-align: center;
  padding: 32px;
  padding-bottom: 128px;
}

@keyframes slidein {
  from {
    /*border: 2px solid white;*/
    background: transparent;
  }

  to {
    /*border: 2px solid black;*/
    background: black;
  }
}

.aya-gallery-zoom-item {
  position: fixed;
  z-index: 10;
  background: red;
  transition: 0.3s all ease-out;
  overflow: hidden;
  animation: 0.3s slidein;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

/*
.aya-gallery-zoom-item.active {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
*/

.aya-gallery-single {
  position: fixed;
  top: 80px;
  left: 0;
  min-height: calc(100vh - 80px);
  min-width: 100vw;
  background: black;
  display: flex;
  align-items: center;
}

.aya-gallery-single-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 128px;
  max-width: 1280px;
  margin: auto;
}

.aya-gallery-single-collection {
  font-family: Rubik;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.02em;
  color: #CCCCCC;
}

.aya-gallery-single-info {
  padding-top: 64px;
}

.aya-gallery-single-name {
  font-family: Playfair Display;
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 64px;
  color: #FFFFFF;
  margin-bottom: 24px;
}

.aya-gallery-single-description {
  font-family: Rubik;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  max-width: 480px;
}

.aya-gallery-controllers {
  grid-column: 1/3;
  display: flex;
  gap: 32px;
  justify-content: center;
  font-family: Teko;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 32px;
  color: #ccc;
}

.aya-gallery-controllers > a {
  color: #ccc;
  text-decoration: none;
  display: flex;
  align-items: center;
  transition: 0.2s all;
}

.aya-gallery-controllers > a:hover {
  color: white;
}

.aya-gallery-not-minted-yet {
  color: #ff9292;
  margin-top: 16px;
  font-size: 24px;
  line-height: 1.5;
}

.aya-gallery-controllers--mobile {
  display: none;
}

.aya-gallery-join-discord {
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 6px 48px 2px;

  background: #FFFFFF;
  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 8px;
  text-transform: uppercase;
  text-decoration: none;

  margin-top: 32px;
  font-family: Teko;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 29px;
  /* identical to box height */
  text-align: center;

  color: #000000;
}

.aya-gallery-not-minted-yet--mobile {
  display: none;
}

@media (max-width:  768px) {
  .aya-gallery-not-minted-yet {
    display: none;
  }

  .aya-gallery-not-minted-yet--mobile {
    display: block;
    color: #ff9292;
    margin-top: 14px;
    font-size: 24px;
    line-height: 1.2;
    text-align: center;
  }

  .aya-gallery-not-minted-yet--mobile > p {
    font-size: 16px;
  }

  .aya-gallery-join-discord {
    margin-top: 8px;
    font-size: 18px;
    padding: 6px 16px 2px;
  }


  .aya-gallery-controllers {
    display: none;
  }

  .aya-gallery-controllers--mobile {
    display: flex;
  }

  .aya-gallery-content {
    margin: 0 12px;
  }

  .aya-gallery-padding {
    height: auto;
    min-height: auto;
    width: 100%;
    align-items: center;
  }

  .aya-gallery-items {
    display: flex;
    min-height: min-content;
    gap: 24px;
  }

  .aya-gallery-item {
    width: 120px;
    height: 420px;
  }

  .aya-gallery-item-picture {
    height: 360px !important;
  }

  .aya-gallery-item-picture-container {
    width: 120px;
    height: 360px;
  }

  .aya-gallery-single {
    padding: 0 16px;
    position: absolute;
    box-sizing: border-box;
    padding-bottom: 32px;
  }

  .aya-gallery-single-info {
    padding-top: 16px;
  }

  .aya-gallery-single-content {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .aya-gallery-item-picture-container-copy {
    width: 100%;
    height: 100%;
    border: none;
  }

  .aya-gallery-item-picture-container-copy .aya-gallery-item-picture {
    width: 100% !important;
    height: 400px !important;
  }

  .aya-gallery-info {
    padding: 32px 16px;
  }

  .aya-gallery-header {
    font-weight: 700;
    font-size: 48px;
    line-height: 64px;
  }

  .aya-gallery-title {
    font-family: Teko;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
  }
}
